import { QueryFunctionContext } from '@tanstack/react-query'
import { restApiFetcher } from '@/pkg/libraries/rest-api'
import { type IBook } from '../../models/book.model'

// api get all books
export const booksQueryApi = async (opt: QueryFunctionContext, queryParams?: { search?: string }) => {
    const { search } = queryParams || {}

    try {
        const url = search ? `api/books?search=${encodeURIComponent(search)}` : 'api/books'
        const res = await restApiFetcher
            .get<IBook[]>(url, {
                signal: opt.signal,
                cache: 'force-cache',
                next: { revalidate: 30 },
            })
            .json()

        if (!res) {
            throw new Error('Books not found')
        }

        return res
    } catch (error) {
        console.error('Books API error:', error)
        throw error
    }
}

// api get book by id
export const bookByIdQueryApi = async (opt: QueryFunctionContext, queryParams: { id: number }) => {
    const { id } = queryParams

    try {
        const res = await restApiFetcher
            .get<IBook>(`api/books/${id}`, {
                signal: opt.signal,
                cache: 'force-cache',
                next: { revalidate: 30 },
            })
            .json()

        if (!res) {
            throw new Error(`Book not found: id=${id}`)
        }

        return res
    } catch (error) {
        console.error('Book API error:', error)
        throw error
    }
}
